@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../../fonts/tildaSansLight.woff2") format("woff2"), url("../../fonts/tildaSansLight.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/tildaSansRegular.woff2") format("woff2"), url("../../fonts/tildaSansRegular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/tildaSansMedium.woff2") format("woff2"), url("../../fonts/tildaSansMedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Tilda Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/tildaSansBold.woff2") format("woff2"), url("../../fonts/tildaSansBold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  margin: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 56px;
  }
  .wrapper--services {
    margin: 0 57px;
  }
  .wrapper--contacts {
    margin: 0 0 0 57px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    margin: 0 90px;
  }
  .wrapper--partners, .wrapper--drafts {
    margin: 0 32px;
  }
  .wrapper--contacts {
    margin: 0 0 0 90px;
  }
}

.title {
  margin: 0 0 20px;
  font-size: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 38px;
  }
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.link {
  text-decoration: none;
  margin: 0;
  color: #000000;
}

.button {
  display: block;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

.page {
  height: 100%;
  font-family: "Tilda Sans", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.page__body {
  margin: 0;
}

.body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #ffffff;
  color: #333333;
}

.body__header {
  margin-bottom: 90px;
}

.body__up-button {
  display: none;
  position: fixed;
  z-index: 1;
  padding: 0;
  bottom: 122px;
  right: 5px;
  width: 50px;
  height: 50px;
  border: 3px solid rgba(0, 0, 0, 0.75);
  border-radius: 50%;
  box-shadow: -5px 5px 5px 0 rgba(0, 0, 0, 0.2);
}

@media (min-width: 768px) {
  .body__up-button--show {
    display: block;
  }
}

.body__up-button-icon {
  fill: rgba(0, 0, 0, 0.75);
}

.header {
  display: flex;
  align-items: flex-end;
  min-height: 844px;
  background: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url("../image/background-image.jpg");
  background-size: cover;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .header {
    min-height: 803px;
  }
}

.header__wrapper {
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
  padding: 0 20px 20px;
}

.header__slogan {
  margin: 0 auto;
  font-size: 30px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 45px;
  letter-spacing: 1px;
}
@media (min-width: 768px) {
  .header__slogan {
    font-size: 68px;
    width: 94%;
    line-height: normal;
    margin-bottom: 24px;
  }
}
@media (min-width: 1280px) {
  .header__slogan {
    font-size: 58px;
    width: 100%;
    max-width: 780px;
  }
}

.header__text {
  margin: 0 auto 277px;
  line-height: 30px;
  font-weight: 300;
}
@media (min-width: 768px) {
  .header__text {
    font-size: 20px;
    width: 94%;
  }
}
@media (min-width: 1280px) {
  .header__text {
    font-size: 20px;
    width: 100%;
    max-width: 780px;
  }
}

.header__button {
  margin: 0 auto;
  transition: transform 0.6s;
}

.header__button--transform {
  transform: translateY(-5px);
}

.header__arrow {
  fill: #ffffff;
  width: 27px;
  height: 13px;
}

.main {
  flex-grow: 1;
}

.main__about-us,
.main__services,
.main__partners,
.main__drafts {
  margin-bottom: 90px;
}
@media (min-width: 768px) {
  .main__about-us,
.main__services,
.main__partners,
.main__drafts {
    margin-bottom: 110px;
  }
}
@media (min-width: 1280px) {
  .main__about-us,
.main__services,
.main__partners,
.main__drafts {
    margin-bottom: 135px;
  }
}

.about-us__intro {
  width: 95%;
  margin: 0 auto 80px;
  font-size: 20px;
  font-weight: 300;
}
@media (min-width: 768px) {
  .about-us__intro {
    font-size: 30px;
    width: 100%;
  }
}
@media (min-width: 1280px) {
  .about-us__intro {
    width: 100%;
    max-width: 620px;
    font-size: 29px;
    margin: 0 auto 80px;
  }
}

.about-us__description {
  width: 95%;
  margin: 0 auto;
  font-size: 14.5px;
  line-height: 1.45;
}
@media (min-width: 768px) {
  .about-us__description {
    width: 100%;
    font-size: 18px;
  }
}
@media (min-width: 1280px) {
  .about-us__description {
    font-size: 18.5px;
    max-width: 620px;
  }
}

.services {
  background-color: #ebebeb;
  padding: 135px 0;
}

.services__title {
  text-align: center;
}

.services-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (min-width: 1280px) {
  .services-list {
    max-width: 620px;
    margin: 0 auto;
  }
}

.services-list__item {
  min-height: 50px;
}
@media (min-width: 768px) {
  .services-list__item {
    min-height: 60px;
  }
}

.services-list__item-text {
  position: relative;
  display: block;
  font-weight: 700;
  line-height: 1.45;
  padding-left: 88px;
}
.services-list__item-text::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
}
@media (min-width: 768px) {
  .services-list__item-text::before {
    width: 60px;
    height: 60px;
  }
}
.services-list__item-text--map::before {
  background: url("../image/structure-map.svg") no-repeat;
}
.services-list__item-text--scenario-text-pater::before {
  background: url("../image/scenario-text-pater.svg") no-repeat;
  top: -4px;
}
@media (min-width: 768px) {
  .services-list__item-text--scenario-text-pater::before {
    top: -8px;
  }
}
.services-list__item-text--megaphone::before {
  background: url("../image/megaphone.svg") no-repeat;
  top: -4px;
}
@media (min-width: 768px) {
  .services-list__item-text {
    font-size: 16px;
  }
}

.partners {
  text-align: center;
}

.partners__title {
  text-align: center;
  margin: 0 28px;
}
@media (min-width: 768px) {
  .partners__title {
    margin-bottom: 24px;
  }
}

.partners__description {
  font-size: 17.5px;
  line-height: 1.4;
  font-weight: 300;
}
@media (min-width: 768px) {
  .partners__description {
    font-size: 22px;
    width: 84%;
    margin: 0 auto 45px;
  }
}
@media (min-width: 1280px) {
  .partners__description {
    max-width: 622px;
    width: 100%;
  }
}

.partners-list {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .partners-list {
    max-width: 960px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 40px 20px;
  }
}

.partners-list__title {
  font-size: 16px;
  font-weight: 700;
  margin: 0 auto;
}
.partners-list__title--stoppav {
  width: 73%;
  margin: 0 auto;
}
.partners-list__title--rusatom {
  width: 76%;
}
@media (min-width: 768px) {
  .partners-list__title {
    font-size: 28px;
    margin: 0 auto 25px;
  }
  .partners-list__title--stoppav {
    width: 100%;
  }
  .partners-list__title--rusatom {
    width: 80%;
  }
}
@media (min-width: 1280px) {
  .partners-list__title {
    font-size: 16px;
  }
  .partners-list__title--stoppav {
    width: 94%;
  }
}

.partners-list__description {
  font-size: 12px;
  line-height: 1.45;
  font-weight: 300;
  width: 87%;
  margin: 0 auto;
}
.partners-list__description--vgasu {
  width: 73%;
}
@media (min-width: 768px) {
  .partners-list__description {
    font-size: 14px;
  }
}

.partners-list__link {
  font-size: 24px;
}

.partners-list__logo {
  width: 100px;
  height: 100px;
  object-fit: contain;
}
.partners-list__logo--govvrn {
  object-fit: cover;
}
@media (min-width: 768px) {
  .partners-list__logo {
    width: 150px;
    height: 150px;
  }
}

.drafts__title {
  text-align: center;
}
@media (min-width: 768px) {
  .drafts__title {
    margin-bottom: 24px;
  }
}

@media (min-width: 768px) {
  .drafts__list {
    padding: 0 7px;
  }
}

.drafts-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .drafts-list {
    max-width: 960px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.drafts-list__item {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 20px;
}
@media (min-width: 1280px) {
  .drafts-list__item {
    gap: 40px;
  }
}

.drafts-list__logo {
  object-fit: contain;
}
@media (min-width: 768px) {
  .drafts-list__logo {
    width: 50px;
    height: 50px;
  }
}
@media (min-width: 1280px) {
  .drafts-list__logo {
    width: 100px;
    height: auto;
  }
}

.drafts-list__title {
  margin: 0;
  font-size: 16px;
  line-height: 1.35;
  font-weight: 700;
}
.drafts-list__title--sgio {
  width: 81%;
}
.drafts-list__title--legacy {
  width: 81%;
}
.drafts-list__title--gisgmp {
  width: 89%;
}
@media (min-width: 768px) {
  .drafts-list__title {
    font-size: 18px;
    width: 100%;
  }
}

.drafts-list__description {
  font-size: 14.5px;
  line-height: 1.45;
  font-weight: 300;
}
@media (min-width: 768px) {
  .drafts-list__description {
    font-size: 16px;
  }
}

.contacts {
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .contacts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 20px;
    padding: 0;
  }
}
@media (min-width: 1280px) {
  .contacts {
    grid-template-columns: 640px 1fr;
  }
}

.contacts__title {
  padding: 30px 0 0;
  color: #ffffff;
  font-size: 24px;
}
@media (min-width: 768px) {
  .contacts__title {
    padding: 0;
  }
}

.contacts__contact-address {
  font-style: normal;
}

.contacts__phone,
.contacts__email {
  display: block;
  font-size: 20px;
  line-height: 1.4;
  color: #ffffff;
}

.contacts__email {
  margin-bottom: 30px;
}

.contacts__address-text {
  margin: 0 0 45px;
}

.contacts__map {
  display: block;
  width: 100%;
  border: none;
}